/* Reach UI Modal */

:root {
  --reach-dialog: 1;
}

[data-reach-dialog-overlay] {
  background: hsla(0, 0%, 0%, 0.33);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  z-index: 40;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}

.fade-in {
  -webkit-animation: fade-in 0.2s ease-out;
  animation: fade-in 0.2s ease-out;
}

[data-reach-dialog-content] {
  width: 85vw;
  margin: 10vh auto;
  background: white;
  padding: 1.5rem;
  outline: none;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.splash-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 70px;
  text-indent: -9999em;
  overflow: hidden;
  pointer-events: none;
  width: 100vw;
  height: 100vh;
}

@media (prefers-reduced-motion: no-preference) {
  .splash-screen {
    -webkit-animation: splash-screen-animation 0.5s;
    animation: splash-screen-animation 0.5s;
  }
}

@-webkit-keyframes splash-screen-animation {
  from {
    -webkit-transform: scale(0.6) translateX(100%);
    transform: scale(0.6) translateX(100%);
  }

  to {
    -webkit-transform: scale(1) translateX(-100%);
    transform: scale(1) translateX(-100%);
  }
}

@keyframes splash-screen-animation {
  from {
    -webkit-transform: scale(0.6) translateX(100%);
    transform: scale(0.6) translateX(100%);
  }

  to {
    -webkit-transform: scale(1) translateX(-100%);
    transform: scale(1) translateX(-100%);
  }
}

